.InvitationModalConfirmStepRow{display:flex}.InvitationModalConfirmStepRow .username-or-icon{width:50%;display:flex;align-items:center}.InvitationModalConfirmStepRow .username-or-icon .email{margin:0 4px 0 8px;overflow-wrap:anywhere;word-break:break-word}.InvitationModalConfirmStepRow .username-or-icon .name{margin:0 4px 0 8px}.InvitationModalConfirmStepRow .username-or-icon .mail-icon{width:36px;height:36px;border-radius:18px;background:var(--button-bg);padding:3px;margin:12px 0 12px 12px;display:inline-block}.InvitationModalConfirmStepRow .username-or-icon .mail-icon svg{width:24px;height:24px;fill:var(--button-color);margin:3px}.InvitationModalConfirmStepRow .username-or-icon .alert-icon{width:36px;height:36px;border-radius:18px;background:var(--dnd-indicator);padding:3px;margin:12px 0 12px 12px;display:inline-block}.InvitationModalConfirmStepRow .username-or-icon .alert-icon svg{width:28.8px;height:28.8px;fill:var(--button-color);margin:.6px}.InvitationModalConfirmStepRow .username-or-icon .Avatar{margin:12px 0 12px 12px}.InvitationModalConfirmStepRow .username-or-icon .Badge{margin-top:2px;display:inline-block;vertical-align:top}.InvitationModalConfirmStepRow .reason{display:flex;align-items:center;width:50%;padding:12px}
