.Menu{z-index:10000}.Menu .dropdown-menu{-webkit-border-radius:4px;-moz-border-radius:4px;-ms-border-radius:4px;-o-border-radius:4px;border-radius:4px;display:block;max-height:80vh;min-width:220px;max-width:270px;padding:8px 0}.Menu .dropdown-menu ul{margin:0;padding:0}.Menu .dropdown-menu li{list-style:none}.Menu .dropdown-menu li a{color:inherit}.Menu.Menu-enter .dropdown-menu{opacity:0}@media screen and (min-width: 768px){.Menu.Menu-enter .dropdown-menu{max-height:0;overflow:hidden;overflow-y:hidden}}.Menu.Menu-enter-active .dropdown-menu{opacity:1}@media screen and (min-width: 768px){.Menu.Menu-enter-active .dropdown-menu{max-height:80vh;transition:max-height 1000ms ease-in, opacity 250ms linear;overflow-y:hidden}}@media screen and (max-width: 640px){.Menu.Menu-enter-active .dropdown-menu{transition:opacity 1000ms linear}}.Menu.Menu-enter-done .dropdown-menu{opacity:1}@media screen and (min-width: 768px){.Menu.Menu-enter-done .dropdown-menu{max-height:80vh;overflow-y:auto}}.Menu.Menu-exit .dropdown-menu{opacity:0}@media screen and (min-width: 768px){.Menu.Menu-exit .dropdown-menu{max-height:0;transition:max-height 1000ms ease-in 0ms, opacity 250ms linear 750ms;overflow-y:hidden}}@media screen and (max-width: 768px){.Menu.Menu-exit .dropdown-menu{transition:opacity 1000ms linear}}
