.Avatar{border-radius:50%}.Avatar.Avatar-xs{width:16px;min-width:16px;height:16px}.Avatar.Avatar-sm{width:24px;min-width:24px;height:24px}.Avatar.Avatar-md{width:32px;min-width:32px;height:32px}.Avatar.Avatar-lg{width:36px;min-width:36px;height:36px}.Avatar.Avatar-xl{width:50px;min-width:50px;height:50px}.Avatar.Avatar-xxl{width:128px;min-width:128px;height:128px}
