.toast{position:absolute;margin:10px 15px 0px;width:calc(100% - 30px);font-size:13px;-webkit-border-radius:4px;-moz-border-radius:4px;-ms-border-radius:4px;-o-border-radius:4px;border-radius:4px;cursor:default;opacity:0;visibility:hidden;background-color:var(--button-bg);color:var(--button-color);height:32px;font-weight:bold;z-index:1}.toast svg{fill:var(--button-color);vertical-align:middle}body.enable-animations .toast{-webkit-transition-property:opacity,visibility;-moz-transition-property:opacity,visibility;-o-transition-property:opacity,visibility;transition-property:opacity,visibility;-webkit-transition-duration:0s;-moz-transition-duration:0s;-o-transition-duration:0s;transition-duration:0s;-webkit-transition-timing-function:ease-in-out;-moz-transition-timing-function:ease-in-out;-o-transition-timing-function:ease-in-out;transition-timing-function:ease-in-out}.toast .toast__jump{position:absolute}.toast .toast__jump svg{margin-right:5px;vertical-align:middle}.toast .toast__dismiss{cursor:pointer;position:absolute;right:0px;padding:0px 8px;height:32px}.toast .toast__dismiss:hover{background-color:rgba(0,0,0,0.2)}.toast .toast__dismiss span{line-height:31px}.toast .toast__message{text-align:center;position:absolute;width:calc(100% - 40px);padding-left:10px;z-index:-1;height:32px;line-height:31px}.toast .toast__message.toast__pointer{cursor:pointer}.toast .toast__message.toast__pointer:hover{background-color:rgba(0,0,0,0.2)}.toast__visible{opacity:1;visibility:visible}body.enable-animations .toast__visible{-webkit-transition-property:opacity,visibility;-moz-transition-property:opacity,visibility;-o-transition-property:opacity,visibility;transition-property:opacity,visibility;-webkit-transition-duration:0s;-moz-transition-duration:0s;-o-transition-duration:0s;transition-duration:0s;-webkit-transition-timing-function:ease-in-out;-moz-transition-timing-function:ease-in-out;-o-transition-timing-function:ease-in-out;transition-timing-function:ease-in-out}.toast__history{background-color:#d8d8d8;color:#3d3c40}.toast__history svg{fill:#3d3c40}.toast__red{background-color:#d64946}@media screen and (max-width: 768px){.toast .toast__message{display:inline-block}}.tooltip__shortcut--txt{opacity:0.6}
