.modal-marketplace{display:flex;flex-direction:column;padding-top:50px;width:100%;height:100%;align-items:center;font-size:16px;color:var(--center-channel-color-90)}@media (max-width: 768px){.modal-marketplace{padding-left:15px;padding-right:15px}}.modal-marketplace .sidebar__search-clear{top:32px;right:38px;font-size:22px;width:30px;height:30px;display:flex;align-items:center;justify-content:center}.modal-marketplace .nav-tabs{font-size:14px}.modal-marketplace .nav-tabs>li>a{-webkit-transition:all .15s ease;-webkit-transition-delay:0s;-moz-transition:all .15s ease;-o-transition:all .15s ease;transition:all .15s ease false;padding:10px 16px}.modal-marketplace h1{font-size:28px;margin:8px 0 24px}.modal-marketplace h1 strong{font-weight:400}.modal-marketplace h2{font-weight:300}.modal-marketplace h2 strong{font-weight:400}.modal-marketplace .more-modal__list .more-modal__row{align-items:normal}.modal-marketplace .more-modal__list .more-modal__row .tag{padding:3px 5px;margin-left:10px;background-color:rgba(0,0,0,0.3);border-radius:4px;font-weight:bold;font-size:0.8em;color:#fff;cursor:help}.modal-marketplace .more-modal__list .more-modal__row .update{border-top:1px solid rgba(0,0,0,0.1);padding:10px 10px 0 0;margin:10px 10px 0 0;font-size:.9em}.modal-marketplace .more-modal__list .more-modal__description{margin:2px 0 0;font-size:.9em}.modal-marketplace .search_input{flex:1;margin-left:16px;margin-right:16px;margin-bottom:20px;margin-top:28px;height:40px;width:720px}.modal-marketplace .btn{font-weight:600;min-width:8rem;border-radius:4px;font-size:13px;padding:8px 16px}.modal-marketplace .btn-outline{background:transparent;border:1px solid var(--button-bg)}.modal-marketplace .tabs{width:720px;display:flex;flex-direction:column}.modal-marketplace .subtitle{margin-left:5px}.modal-marketplace .plugin__list{display:flex;flex-direction:column}.modal-marketplace .plugin__list .plugin__row{border-bottom:1px solid rgba(0,0,0,0.15);display:flex;padding:10px 15px;align-items:center}.modal-marketplace .plugin__list .plugin__row.clickable{cursor:pointer}.modal-marketplace .plugin__list .plugin__row:hover .plugin__actions--round{opacity:.5}.modal-marketplace .more-modal__list{padding-bottom:80px}.modal-marketplace .icon__plugin{display:flex;align-items:center;justify-content:center;height:42px;flex:0 0 42px;margin-right:4px;border-radius:50%}.modal-marketplace .icon__plugin svg{width:32px;height:32px}.modal-marketplace .icon__plugin svg{fill:var(--button-bg)}.modal-marketplace .icon__plugin--background{background-color:#fff;padding:6px}.modal-marketplace .icon__plugin--background svg{width:24px;height:24px}.modal-marketplace .no_plugins_div{text-align:center}.modal-marketplace .item_error{background-color:var(--error-text-08)}.modal-marketplace .error_text{color:var(--error-text);opacity:1}.error-bar{background-color:var(--center-channel-bg);color:var(--error-text);min-height:32px;position:fixed;top:0;width:100%;z-index:8;overflow:hidden;padding:5px 30px;max-height:32px}.error-bar .error-bar__content{background-color:var(--error-text-12);position:absolute;left:0;top:0;width:100%;height:100%;display:flex;align-items:center;justify-content:center}
