.FullScreenModal{position:fixed;top:0;left:0;width:100%;height:100%;background:var(--center-channel-bg);z-index:1030 !important;overflow:auto}.FullScreenModal .close-x,.FullScreenModal .back{border:0;background:none;padding:0;position:fixed;top:48px;height:40px;width:40px;cursor:pointer;z-index:9}.FullScreenModal .close-x svg,.FullScreenModal .back svg{fill:var(--center-channel-color-90);padding:4px;width:40px;height:40px;border-radius:4px}.FullScreenModal .close-x svg:hover,.FullScreenModal .back svg:hover{background:var(--button-bg-10)}.FullScreenModal .close-x{right:48px}.FullScreenModal .back{left:48px}.FullScreenModal-enter,.FullScreenModal-appear{opacity:0}.FullScreenModal-enter-active,.FullScreenModal-appear-active{opacity:1;transition:opacity 100ms}.FullScreenModal-exit{opacity:1}.FullScreenModal-exit-active{opacity:0;transition:opacity 100ms}
